@import "../../base/mixin.scss";
.testimonials {
  grid-column: full-start / full-end;
  .bgImage {
    background-image: url("../../assets/images/testimonial/bg.jpg");
  }
  .bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }
  &__image {
    height: 100px;
    width: 100px;
    margin: auto;
    margin-bottom: 3rem;
    img {
      border-radius: 100px;
    }
  }
  .swiper-container {
    width: 100%;
    background: #000;
  }
  .swiper-wrapper {
    z-index: 99;
  }
  .swiper-slide {
    font-size: 18px;
    color: #fff;
    box-sizing: border-box;
    .title {
      font-size: 1.6rem;
      font-weight: 600;
      margin: 1rem;
    }
    .subtitle {
      font-size: 1.4rem;
      text-transform: capitalize;
      display: inline-block;
    }
    .text {
      font-size: 1.7rem;
      font-style: italic;
      line-height: 3rem;
      margin: auto;
      color: #fff;
      padding: 0 1.5rem;
      @include medium {
        max-width: 53%;
      }
      @include large {
        max-width: 40%;
      }
    }
  }
}
